import React from "react";
import "./Header.css";

export default function Headerr() {
  return (
    <header>
      <div class="container text-center">
        <div class="row">
          <div class="col-md-7 col-sm-12">
            <h1>Amazing Basketball Tournament</h1>
            <p>Go fight for it team.</p>
            <button class="btn btn-light px-5 py-2">Join Now</button>
          </div>
          <div class="col-md-5 col-sm-12 ">
            <img
              className="logo"
              src="https://raw.githubusercontent.com/hiteshchoudhary/LCO-sports-web-challenge/master/sports-logo-transparent.png"
              alt="Books"
            />
          </div>
        </div>
      </div>
    </header>
  );
}
