import React from "react";
import "./card.css";

export default function Card(props) {
  console.log(props);
  return (
    <div className="card mr-2 shadow-lg d-inline-block text-center">
      <img
        src={props.user.imageUrl}
        alt=""
        className="card-img-top border-radius p-4"
      />
      <div className="card-body">
        <h3 class="card-title">{props.user.name}</h3>
        <p class="card-text">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Numquam rem
          repellat similique sit obcaecati quis atque in, placeat velit nisi
          laboriosam incidunt? Minima iusto error doloremque repellat nihil quam
          optio.
        </p>
        <a href="#/" class="text-secondary text-decoration-none">
          Complete Profile
        </a>
        <p class="text-black-50 ">Organiser</p>
      </div>
    </div>
  );
}
